.rate
  &__star
    margin-right: 0.2857rem
    cursor: pointer

    &--selected
      margin-right: 0.2857rem
      path
        fill: url(#paint0_linear)
        stroke: url(#paint0_linear)

    &:last-child
      margin-right: 0

    path
      fill: none
      stroke: url(#paint0_linear)

